.App {
  text-align: center;
  display: inherit;
}

.imgBtn{
  width: 40px;
  height: 40px;
  border-radius: 2px;
}

.imgCart{
  width: 40px;
  height: 40px;
}

 button{
  background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
}

.imgBtn:hover{
  background-color: lightpink;
  border: 0.5px solid purple;
}

.inline{
  display:inline-block;
}
 .alignleft {
  float: left;
  margin-left: 30px;
  margin-top: 10px;
}
.alignright {
  float: right;
  margin-right: 30px;
  margin-top: 10px;
}

.imgGirl{
  width: 100px;
  height: 100px;
}

h1{
  color: white;
  font-family: Georgia, serif;
  text-shadow: 3px 3px 6px  #e6004c;
}

p{
  color: #e6004c;
}

#buyBtn{
  padding: 5px;
  border: 1.5px solid #e6004c;
  background-color: #ff99ff;
  border-radius: 2px;
}

#buyBtn:hover{
  background-color: lightpink;
}

#buyBtn:active{
  border: 2px solid #e6004c;
}

.inCart{
  z-index: 100;
  position:relative;
  font-size: 10px;
  margin-left: -38px;
  margin-top: -1.5px;
}